@font-face {
  font-family: Redaction-35;
  src: url(/static/media/Redaction35-BWXjw.9d540230.otf);
}

@font-face {
  font-family: Beton-Bold;
  src: url(/static/media/BetonP-ExtBol.b5f98e34.otf);
}

@font-face {
  font-family: Beton-Open-Regular;
  src: url(/static/media/BetonOpen-Regular.711e41c1.otf);
}

@font-face {
  font-family: Junicode;
  src: url(/static/media/Junicode.817a5ee3.ttf);
}

@font-face {
  font-family: Junicode-Italic;
  src: url(/static/media/Junicode-Italic.6ca7e845.ttf);
}

@font-face {
  font-family: Junicode-Bold;
  src: url(/static/media/Junicode-Bold.53f4c3ac.ttf);
}

@font-face {
  font-family: Junicode-Bold-Italic;
  src: url(/static/media/Junicode-BoldItalic.9ed76019.ttf);
}

#sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: white;
  padding: 20px;
}

#italicize {
  font-family: Junicode-Italic;
}

body {
  font-family: Junicode;
  font-size: 1.25em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: url(http://www.javascriptkit.com/dhtmltutors/cursor-hand.gif), auto;
}

#issue-number {
  margin: 10px;
}

#magazine-title-link {
  text-decoration: none;
  color: black;
}

#fixed-header {
  position: fixed;
  width: 100%;
  background-color: white;
  margin-top: -20px;
  padding: 20px;
}

#footer {

}

#content {
  margin: 20px 0 20px 0;
}

#wrapper-about {
  background-color: #fff44c;
}

#issue-number-and-date {
  font-size: 1em;
}

#about-title {
  font-family: Beton-Bold;
  font-size: 5em;
}

#about-text {
  width: 50%;
  margin: 0 auto;
}

#submission-title {
  font-family: Beton-Open-Regular;
}

#submission-author {
  font-family: Junicode-Italic;
}

#submission-footnote {
  margin: 20px 0 20px 0;
  font-family: Junicode-Italic;
}

#navigation-links {
  margin-left: auto;
  margin-right: 20px;
  font-family: Redaction-35;
}

#navigation-link {
  margin: 20px;
  padding: 10px;
  text-decoration: none;
  border: 2px solid transparent;
  color: black;
}

#navigation-link:hover {
  background-color: blue;
  color: white;
}

#magazine-title {
  font-family: Redaction-35;
  font-size: 2em;
  margin: 0;
}

#magazine-subtitle {
  font-family: Junicode-Italic;
  font-size: 1em;
  margin: 0;
}

#submission-image-large {
  width: 100%;
  height: auto;
  margin: 20px 0 20px 0;
}

#submission-image-medium {
  width: 75%;
  height: auto;
  margin: 20px 0 20px 0;
}

#submission-image-small {
  width: 50%;
  height: auto;
  margin: 20px 0 20px 0;
}

